.reviews-and-questions {
  @include clearfix;
  @media (min-width: $mobile) {
    font-size: 0;
  }

  &__item {
    float: left;
    width: 100%;

    &:first-child {
      float: right;
    }

    @media (min-width: $mobile) {
      width: 50%;
      display: inline-block;
      vertical-align: top;

      &:first-child {
        float: none;
      }

      &--questions {
        background-color: #f8fbff;
      }

      &--reviews {
        background-color: #f5f5f5;
      }
    }
  }
}
