.slider {
  width: 100%;

  &__row {
    @media (min-width: 600px) {
      @include clearfix;
      text-align: center;

      &--one {
        min-height: 275px;

        & .slider__item {
          & p {
            max-width: 198px;
            margin: 0 auto;
          }
        }
      }

      &--one,
      &--three {
        padding-left: 22%;
        padding-right: 22%;

        & .slider__item {
          width: 50%;
        }
      }

      &--two {
        min-height: 200px;
        & .slider__item {
          width: 25%;

          &:last-child {
            float: right;
          }

          & p {
            max-width: 198px;
            margin: 0 auto;
          }
        }
      }

      &--three {
        & .slider__item:first-child {
          & p {
            max-width: 198px;
            margin: 0 auto;
          }
        }
      }
    }
  }

  &__item {
    position: relative;
    text-align: center;
    padding-top: 103px;

    @media (min-width: 600px) {
      float: left;
      min-height: 170px;
    }

    &--icon-81::before,
    &--icon-82::before,
    &--icon-83::before,
    &--icon-84::before {
      position: absolute;
      content: "";
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: 0 0;
    }

    &--icon-81::before {
      width: 82px;
      height: 88px;
      background-image: url(../img/icons/icon-81.svg);
    }

    &--icon-82::before {
      width: 81px;
      height: 96px;
      background-image: url(../img/icons/icon-82.svg);
    }

    &--icon-83::before {
      width: 73px;
      height: 88px;
      background-image: url(../img/icons/icon-83.svg);
    }

    &--icon-84::before {
      width: 113px;
      height: 103px;
      background-image: url(../img/icons/icon-84.svg);
    }

    & p {
      max-width: 199px;
      margin: 0 auto;
    }
  }
}
