.logo {
  display: block;
  width: 100%;
  max-height: 93px;

  & img {
    display: block;
    max-width: 100%;
    width: 216px;

    height: auto;

    margin: 0 auto;
    object-fit: contain;

    -ms-interpolation-mode: bicubic;

    @media (min-width: $mobile) {
      width: 316px;
      height: 93px;
    }
  }
}
