.accordion {
  padding-top: 30px;
  max-width: 725px;
  width: 100%;
  &__item {
    position: relative;

    border-bottom: 1px solid $c-grey-light;

    &.active {
      & h3 {
        color: $c-green;
      }

      & .accordion__item-text {
        // display: block;
        padding-bottom: 12px;
        padding-right: 40px;
        font-size: 16px;
        line-height: 150%;
        height: auto;

        @media (min-width: $mobile) {
          padding-left: 70px;
          margin-top: -10px;
        }
      }

      & .accordion__item-icon {
        transform: rotate(180deg);
        transition: $transition;
      }
    }
  }

  &__item-header {
    position: relative;
    font-size: 0;
    padding-top: 12px;
    padding-bottom: 12px;

    &:hover {
      cursor: pointer;
    }

    & svg,
    & h3,
    &__item-icon {
      display: inline-block;
      vertical-align: middle;
    }

    & svg {
      width: 45px;
      fill: $c-light;

      display: none;

      @media (min-width: $mobile) {
        display: inline-block;
      }
    }

    & h3 {
      margin: 0;

      font-family: $font;
      font-style: normal;
      font-weight: bold;

      color: $c-dark;

      width: 90%;
      font-size: 16px;
      line-height: 150%;

      @media (min-width: $mobile) {
        width: 80%;
      }

      @media (min-width: $tablet) {
        padding-left: 20px;
        width: calc(100% - 45px - 40px);
        font-size: 24px;
        line-height: 28px;
      }
    }
  }

  &__item-icon {
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translateY(-50%);
    width: 18px;
    height: 18px;
    fill: transparent;
    margin-left: auto;
    transition: $transition;

    @media (max-width: $mobile) {
      padding-top: 0;
    }

    & svg {
      display: inline-block;
    }
  }

  &__item-text {
    // display: none;
    font-size: 0;
    padding: 0;
    margin: 0;
    height: 0;
  }
}
