.questions {
  padding: 40px 1rem 0;
  background-color: #f8fbff;

  @media (min-width: $stop-scaling) {
    padding: 100px 85px;
  }

  @media (min-width: $mobile) {
    padding: rem(80px) rem(40px);
  }

  @media (min-width: 1480px) {
    padding: rem(100px) rem(85px);
  }

  &__wrapper {
    max-width: 800px;
  }

  &__header {
    text-align: center;
  }

  &__title {
    @include title;

    max-width: 420px;
    margin: 0 auto;
  }
}
