.reviews {
  padding: 40px 1rem 0;
  background-color: #f5f5f5;

  @media (min-width: $stop-scaling) {
    padding: 100px 85px 0;
  }

  @media (min-width: $mobile) {
    padding: rem(80px) rem(40px);
  }

  @media (min-width: 1480px) {
    padding: 6rem 4rem 6rem 7rem;
  }


  &__wrapper {
    max-width: 800px;
    margin-left: auto;
  }

  &__title {
    @include title;
  }

  &__item-guotes {
    padding-top: rem(44px);
    padding-bottom: rem(35px);
    font-family: $font-alt;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;

    @media (min-width: $mobile) {
      padding-top: rem(48px);
      padding-bottom: rem(70px);
    }

    & .review__pic {
      width: 97px;
      margin: 0 auto -48px;
      display: inherit;
      position: relative;
      z-index: 1;

      @media (min-width: $tablet) {
        width: 210px;
        height: 210px;
        margin: 0;
        float: left;
      }
    }

    & .review__info {
      position: relative;
      padding: 16px;
      padding-top: rem(80px);
      width: 100%;
      text-align: center;

      background: #ffffff;
      box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.24);
      border-radius: 32px;

      &::before,
      &::after {
        content: "";
        position: absolute;
        top: 24px;
        left: 16px;
        width: 40px;
        height: 26px;
        background-image: url(../img/icons/icon-quotes2.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }

      &::after {
        right: 16px;
        left: auto;
        transform: rotate(180deg);
      }

      @media (min-width: $tablet) {
        padding: 0 rem(25px) 0 rem(50px);
        width: calc(100% - 210px);
        box-shadow: none;
        border-radius: 0;
        background-color: transparent;
        text-align: left;

        &::before,
        &::after {
          top: 0;
          left: 0;
          width: 40px;
          height: 26px;
          background-image: url(../img/icons/icon-quotes.svg);
        }

        &::after {
          bottom: 0;
          right: 0;
          left: auto;
          top: auto;
          transform: rotate(180deg);
        }
      }
    }
  }

  &__items {
    font-size: 0;

    @media (min-width: $tablet) {
      padding: 4rem 1.2rem 0;
      
    }
  }

  &__item {
    display: inline-block;
    vertical-align: top;
    width: 100%;
    padding-bottom: rem(26px);

    @media (min-width: $tablet) {
      width: 47%;
      padding-bottom: 0;
    }
  }
}
