.review {
  font-size: 0;

  &--revers {
    @include clearfix;

    & .review__pic {
      float: left;
    }
    & .review__info {
      float: right;
    }
  }

  &__pic {
    display: inline-block;
    vertical-align: top;
    width: 97px;

    @media (min-width: $tablet) {
      width: 110px;
    }
  }

  &__info {
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 97px);

    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;

    @media (min-width: $tablet) {
      padding-top: 8px;
      width: calc(100% - 110px);
    }
  }

  &__name {
    padding-top: 10px;
    font-family: $font-alt;
    font-weight: 700;
  }
}
