.page-header {
  max-width: 1920px;
  margin: 0 auto;
  @include container;
  font-size: 0;

  text-align: center;

  @media (max-width: $mobile) {
    padding-top: 10px;
  }

  @media (min-width: $mobile) {
    padding-top: rem(35px);
    padding-bottom: rem(30px);
    text-align: left;
  }

  &__logo,
  &__nav,
  &__button {
    display: inline-block;
    vertical-align: middle;
  }

  &__logo {
    width: 100%;

    margin: 0 auto;
    text-align: center;

    @media (min-width: $mobile) {
      width: 20%;
    }
  }

  &__nav {
    display: none;

    @media (min-width: $mobile) {
      width: calc(100% - 20% - 18%);
      display: inline-block;
    }

    @media (min-width: 1280px) {
      width: calc(100% - 20% - 7%);
    }
  }

  &__button {
    display: none;

    @include btn-reset;
    margin-left: auto;
    margin-right: 0;
    padding: 4px rem(20px);
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;

    color: #ffffff;

    background-color: #00c368;
    box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.4);
    border-radius: 8px;

    @media (min-width: $mobile) {
      width: 18%;
      display: inline-block;
    }

    @media (min-width: 1280px) {
      width: 7%;
    }
  }
}
