.promo {
  position: relative;

  padding-top: rem(155px);
  padding-bottom: rem(30px);

  background-image: url(../img/promo/box.png);
  background-repeat: no-repeat;
  background-size: auto 270px;
  background-position: center 0;
  overflow: hidden;

  @media (min-width: 600px) {
    padding-top: rem(79px);
    padding-bottom: rem(88px);

    background-image: url(../img/promo/bg-promo.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__wrapper {
    position: relative;
    @include container;
  }

  &-left {
    display: none;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 300px;
    height: 100%;
    background-image: url(../img/promo/left.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 0 100%;

    @media (min-width: $tablet) {
      display: inline-block;
    }
  }

  &-right {
    display: none;
    position: absolute;

    top: 0;
    right: 0;
    width: 390px;
    height: 100%;
    background-image: url(../img/promo/right.png);
    background-repeat: no-repeat;
    background-size: contain;

    @media (min-width: $tablet) {
      display: inline-block;
    }
  }

  &__title {
    @include title;
    text-transform: uppercase;

    @media (max-width: 600px) {
      &::before {
        background-color: transparent;
      }
    }

    @media (min-width: $tablet) {
      padding-left: 85px;
    }
  }

  &__info {
    position: relative;
    max-width: 950px;

    margin: 0 auto;
    padding-top: 50px;
    overflow: hidden;

    @media (min-width: 600px) {
      padding-top: 70px;
      &::before {
        z-index: 0;
        content: "";
        position: absolute;
        top: 75%;
        left: 50%;

        transform: translate(-50%, -75%);
        width: 65%;
        height: 450px;
        max-width: 100%;
        background-image: url(../img/promo/box.png);
        background-repeat: no-repeat;
        background-position: 50%;
        background-size: 100% 100%;
      }
    }
  }

  & .section__title {
    @media (min-width: $tablet) {
      margin-left: rem(159px);
    }
  }

  &__slider {
    position: relative;
    z-index: 1;
    max-width: 100%;

    & .bx-wrapper {
      box-shadow: none;
      border-radius: 0;
      border: none;
      background-color: transparent;
    }

    & .bx-wrapper .bx-controls-direction a {
      top: 100%;
      margin-top: 20px;
    }

    & .bx-wrapper img {
      margin: 0 auto;
    }

    & .bx-wrapper .bx-prev,
    & .bx-wrapper .bx-next {
      background-image: url(../img/icons/icon-arrow.png);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: 0 0;
      display: inline-block;
      width: 11px;
      height: 19px;
    }

    & .bx-wrapper .bx-next {
      transform: rotate(180deg);
      margin-top: 0;
      margin-bottom: 20px;
    }

    & .bx-wrapper .bx-pager.bx-default-pager a {
      background-color: #e7ebf0;
      width: 8px;
      height: 8px;

      &.active {
        background-color: #a6abbd;
      }
    }
  }
}
