.nav {
  padding-left: 2.5rem;

  &__list {
    @include list-reset;
    font-size: 0;
  }

  &__item {
    display: inline-block;
    vertical-align: middle;

    &:not(:last-child) {
      padding-right: 3.5rem;

      @media (min-width: $tablet) {
        padding-right: 5.5rem;
      }
    }

    &.active {
      & .nav__link {
        padding: 7px rem(27px);
        color: #ffffff;
        background-color: #00c368;
        box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.4);
        border-radius: 8px;
      }
    }
  }

  &__link {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    color: #a6abbd;

    @include hover;
  }
}
