.section {
  &__title {
    position: relative;
    padding-top: 20px;
    font-family: $font-alt;
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 47px;
    text-transform: uppercase;
    text-align: center;

    @media (max-width: $mobile) {
      font-size: 24px;
      line-height: 28px;
    }

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 47%;
      transform: translateX(-47%);
      width: 184px;
      height: 4px;
      background-color: $c-green;
    }
  }
}
