.page {
  position: relative;

  min-height: 100%;

  margin: 0;
  padding: 0;
  font-family: $font;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 150%;
  color: $c-dark;

  background-color: transparent;
  scroll-behavior: smooth;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
